// Main font
@import url('https://fonts.googleapis.com/css?family=Press+Start+2P&display=swap');

* {
    margin: 0;
    padding: 0;
}

body {
	min-width: 1300px;
	background-image: url("assets/background.jpg");
	background-size: cover;
	background-position: center;
	background-repeat: no-repeat;

	&::-webkit-scrollbar {
		display: none;
	}
}